import { prisma } from "../lib/db";
import { addUser, deleteUser } from "./actions";


export default async function Home({
  searchParams,
}: {
  searchParams: Promise<{ q?: string }>;
}) {
  // Get the search query from the URL (e.g., /?q=charlie)
  const query = (await searchParams).q || "";

  // Fetch users that match the name or email
  const users = await prisma.user.findMany({
    where: {
      OR: [
        { name: { contains: query, mode: "insensitive" } },
        { email: { contains: query, mode: "insensitive" } },
      ],
    },
    orderBy: { createdAt: "desc" },
  });

  return (
    <main className="min-h-screen bg-[#09090b] text-zinc-100 p-6 md:p-12 font-sans">
      <div className="max-w-4xl mx-auto">
        
        <header className="mb-12 border-b border-zinc-800 pb-8 flex flex-col md:flex-row md:items-end justify-between gap-6">
          <div>
            <h1 className="text-3xl font-bold tracking-tight text-white">Project Playground</h1>
            <p className="text-zinc-500 mt-2">Managing {users.length} users on Neon Postgres</p>
          </div>

          {/* SEARCH FORM */}
          <form method="GET" className="relative">
            <input 
              name="q"
              defaultValue={query}
              placeholder="Search users..." 
              className="bg-zinc-900 p-2 pl-4 pr-10 rounded-full border border-zinc-800 focus:border-cyan-500 outline-none w-64 text-sm transition-all"
            />
            <button type="submit" className="absolute right-3 top-2.5 text-zinc-500">
               <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><circle cx="11" cy="11" r="8"/><path d="m21 21-4.3-4.3"/></svg>
            </button>
          </form>
        </header>

        {/* ... Rest of your code (Grid, Form, List) stays the same ... */}

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-10">
          
          {/* LEFT COLUMN: REGISTRATION FORM */}
          <section className="lg:col-span-1">
            <div className="sticky top-12 bg-zinc-900/50 p-6 rounded-2xl border border-zinc-800">
              <h2 className="text-lg font-semibold mb-4">Add User</h2>
              <form action={addUser} className="flex flex-col gap-3">
                <input 
                  name="name" 
                  placeholder="Full Name" 
                  className="bg-zinc-950 p-3 rounded-xl border border-zinc-800 focus:border-blue-500 outline-none transition-all text-sm"
                  required 
                />
                <input 
                  name="email" 
                  type="email" 
                  placeholder="Email Address" 
                  className="bg-zinc-950 p-3 rounded-xl border border-zinc-800 focus:border-blue-500 outline-none transition-all text-sm"
                  required 
                />
                <button type="submit" className="mt-2 bg-white text-black font-bold py-3 rounded-xl hover:bg-zinc-200 transition-colors text-sm cursor-pointer">
                  Create Account
                </button>
              </form>
            </div>
          </section>

          {/* RIGHT COLUMN: USER LIST */}
          <section className="lg:col-span-2">
            <div className="grid gap-4">
              {users.length === 0 && (
                <div className="text-center p-12 border border-dashed border-zinc-800 rounded-2xl text-zinc-600">
                  No users in database.
                </div>
              )}
              
              {users.map((user) => (
                <div 
                  key={user.id} 
                  className="group flex items-center justify-between p-5 rounded-2xl bg-zinc-900/30 border border-zinc-800 hover:bg-zinc-900/60 transition-all"
                >
                  <div>
                    <p className="font-semibold text-zinc-100">{user.name}</p>
                    <p className="text-sm text-zinc-500">{user.email}</p>
                    <p className="text-[10px] text-zinc-700 uppercase mt-1 tracking-widest font-bold">
                      Joined {new Date(user.createdAt).toLocaleDateString()}
                    </p>
                  </div>

                  {/* DELETE BUTTON */}
                  <form action={async () => {
                    "use server"
                    await deleteUser(user.id)
                  }}>
                    <button 
                      type="submit"
                      className="opacity-0 group-hover:opacity-100 p-2 text-zinc-500 hover:text-red-400 hover:bg-red-400/10 rounded-lg transition-all cursor-pointer"
                    >
                      <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M3 6h18"/><path d="M19 6v14c0 1-1 2-2 2H7c-1 0-2-1-2-2V6"/><path d="M8 6V4c0-1 1-2 2-2h4c1 0 2 1 2 2v2"/></svg>
                    </button>
                  </form>
                </div>
              ))}
            </div>
          </section>
        </div>

      </div>
    </main>
  );
}